:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
  --button-black: #3a3937;
  --button-white: #fff7e6;
}

.services-title {
  text-align: center;
}
.services .underline:after {
  content: "";
  display: block;
  height: 1px;
  background: var(--button-white);
  width: 200px;
  margin-top: 9px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0px;
}
@media (min-width: 1024px) {
  .services .underline:after {
    width: 300px;
  }
}
.services .services-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1150px;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: auto;
  margin-left: auto;
}
.services .services-wrapper .service {
  text-align: center;
  max-width: 350px;
  margin-top: 0px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 30.25px;
}
.services .services-wrapper .service-icon-wrapper {
  border: 2px solid var(--secondary);
  border-radius: 100px;
  padding: 25px;
  display: inline-block;
  margin-bottom: 9px;
}
@media (min-width: 1024px) {
  .services .services-wrapper .service-icon-wrapper {
    padding: 35px;
  }
}
.services .services-wrapper .service-title {
  font-family: Norwester;
  font-size: 28px;
}
@media (min-width: 1024px) {
  .services .services-wrapper .service-title {
    font-size: 36px;
  }
}
.services .services-wrapper .service-about {
  width: 80%;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: auto;
  margin-left: auto;
  line-height: 1.3;
}
.services .services-wrapper .service-about-popup {
  position: relative;
}
.services .services-wrapper .service-about-popup-icon {
  margin-top: -13px;
  margin-bottom: -6px;
  cursor: pointer;
}
.services .services-wrapper .service-about-popup-close {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}
.services .services-wrapper .service-about-popup-text {
  position: absolute;
  display: none;
  width: 275px;
  text-align: left;
  background: var(--secondary);
  color: black;
  font-size: 15px;
  padding: 10px;
  border-radius: 10px;
  right: -75px;
  top: 35px;
}
@media (min-width: 667px) {
  .services .services-wrapper .service-about-popup-text {
    right: -14px;
    top: 40px;
  }
}
.services .services-wrapper .service-about-popup-text::after {
  content: "";
  position: absolute;
  border-width: 12px;
  border-style: solid;
  border-color: transparent transparent var(--secondary) transparent;
  top: -24px;
  right: 76px;
}
@media (min-width: 667px) {
  .services .services-wrapper .service-about-popup-text::after {
    right: 15px;
    top: -24px;
  }
}
.services .services-wrapper .service-about-popup-text > a {
  display: block;
  margin-top: 9px;
  color: var(--primary);
}
.services .services-wrapper .service-about-popup-text.is-open {
  display: block;
}
@media (min-width: 667px) {
  .services .services-wrapper .service {
    max-width: 33.3333333333%;
  }
}
@media (min-width: 667px) {
  .services .services-wrapper {
    flex-direction: row;
  }
}

:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
  --button-black: #3a3937;
  --button-white: #fff7e6;
}

.base-button {
  background: var(--secondary);
  outline: 0;
  text-transform: capitalize;
  color: rgba(var(--primary), 0.8);
  font-family: Montserrat-SemiBold;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 25px;
  padding-left: 25px;
  border-radius: 15px;
  text-decoration: none;
  font-size: 18px;
  cursor: pointer;
  border: 2px solid var(--secondary);
  transition: background 0.2s ease-out, color 0.2s ease-out;
}
.base-button:hover {
  background: transparent;
  color: var(--secondary);
}
.base-button.black-button {
  background: var(--button-black);
  color: var(--secondary);
  border-color: var(--button-black);
}
.base-button.black-button:hover {
  background: transparent;
}
.base-button.white-button {
  background: var(--button-white);
  color: var(--primary);
  border-color: var(--button-white);
}
.base-button.white-button:hover {
  background: transparent;
  color: var(--button-white);
}
.base-button > .icon {
  margin-right: 4px;
}

body {
  background: var(--primary);
}